@import "../../constants/constants";
@import "../../css/mixins";

.main {
  display: grid;
  grid-template-columns: 1fr;
 
}
.main__header {
  grid-row: 1/4;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.main__slogan {
  font: 700 normal 30px 'Roboto', sans-serif;
  padding-top: 32px;
  padding-left: 64px;
  color: $main_accent;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  @include desktop_min{
    padding-left:32px;
  }
}
.main__sity {
  font: 400 normal 14px 'Roboto', sans-serif;
  color: $dark_gray;
  padding-top: 39px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  width: 100%;
  height: 18px;
}
.main__sity_marker {
  margin-right:5.5px;
}
.main__sity_name {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 64px;
  @include desktop_min{
    margin-right: 29px;
    margin-top:2px;
  }
}

.main__section {
  grid-row: 4/24;
  padding: 146px 64px 64px 64px;
  display: flex;
  flex-direction: column;
  @include desktop_min{
    padding: 146px 32px 64px 32px;
  }
}

.main__section_karshering {
  font: 700 normal 70px 'Roboto', sans-serif;
  color: $main_black;
  line-height: 65.63px;
}
.main__section_slogan {
  font: bold normal 70px 'Roboto', sans-serif;
  color: $main_accent;
  line-height: 66px;
  margin-top:4px;
}

.main__section_minute {
  font: 300 normal 26px 'Roboto', sans-serif;
  color: $dark_gray;
  line-height: 30px;
  margin-top: 25px;
}

.main__section_btn {
  font: 500 normal 18px 'Roboto', sans-serif;
  color: #fff;
  line-height: 21px;
  margin-top: 63px;
  background: linear-gradient(90deg, #0ec261 2.61%, #039f67 112.6%);
  width: 250px;
  height: 48px;
  border-radius: 8px;
  border: 0;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  &:hover {
    filter: brightness(0.9);
  }
  &:active {
    filter: brightness(0.8);
  }
}

.main__footer {
  grid-row: 24/26;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.main__years {
  font: 400 normal 13px 'Roboto', sans-serif;
  line-height: 15px;
  color: $dark_gray;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  margin-bottom: 32px;
  margin-left: 64px;
  @include desktop_min{
    margin-left: 32px;
  }
}
.main__phone {
  font: 400 normal 13px 'Roboto', sans-serif;
  line-height: 15px;
  color: $main_black;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  margin-right: 64px;
  margin-bottom: 32px;
  @include desktop_min{
    margin-right: 32px;
  }
}
